 .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  /* Hide default HTML checkbox */
 ul .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  ul .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
 ul .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  ul input:checked + .slider {
    background-color: #2196F3;
  }
  
 ul input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  ul input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
.slider.round {
  height: 35px;
    border-radius: 34px;
  }
  
 ul .slider.round:before {
    border-radius: 50%;
  }