.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  /* Hide default HTML checkbox */
 div .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  div .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
  }
  
 div .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
  }
  
  div input:checked + .slider {
    background-color: #0B3954;
  }
  
 div input:focus + .slider {
    box-shadow: 0 0 1px #0B3954;
  }
  
  div input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
.slider.round {
  height: 35px;
    border-radius: 34px;
  }
  
 div .slider.round:before {
    border-radius: 50%;
  }
